
.page-rank {
  width: 100%;
  height: 100%;
  background: #FFFFFF;
}
.page-rank .title {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 0.15rem 0.25rem;
  color: #5B1112;
  font-size: 14px;
  font-weight: bold;
}
@media screen and (max-device-width: 320px) {
.page-rank .title {
    font-size: 12px;
}
}
.rank-label {
  font-size: 10px;
  text-anchor: end;
  fill: #5B1111;
}
.page-rank .rank-active {
  -webkit-transform: scale3d(0, 0, 1);
          transform: scale3d(0, 0, 1);
}
.page-rank .rank-active:nth-child(1) {
  -webkit-animation: bobble 0.8s linear 0.1s infinite;
          animation: bobble 0.8s linear 0.1s infinite;
  will-change: opacity;
}
.page-rank .rank-active:nth-child(2) {
  -webkit-animation: bobble 0.8s linear 0.4s infinite;
          animation: bobble 0.8s linear 0.4s infinite;
  will-change: opacity;
}
.page-rank .rank-active:nth-child(3) {
  -webkit-animation: bobble 0.8s  linear 0.6s infinite;
          animation: bobble 0.8s  linear 0.6s infinite;
  will-change: opacity, transform;
}
@-webkit-keyframes bobble {
0% {
    opacity: 0.8;
    -webkit-transform: scale3d(1.3, 1.3, 1);
            transform: scale3d(1.3, 1.3, 1);
}
50% {
    opacity: 0.5;
    -webkit-transform: scale3d(0.9, 0.9, 1);
            transform: scale3d(0.9, 0.9, 1);
}
100% {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@keyframes bobble {
0% {
    opacity: 0.8;
    -webkit-transform: scale3d(1.3, 1.3, 1);
            transform: scale3d(1.3, 1.3, 1);
}
50% {
    opacity: 0.5;
    -webkit-transform: scale3d(0.9, 0.9, 1);
            transform: scale3d(0.9, 0.9, 1);
}
100% {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}



