
.infomation-wrapper {
  position: absolute;
  left: 22px;
  top: 25px;
  padding: 5px;
  z-index: 1000;
  width: 4.2rem;
  font-size: 12px;
  background: #fff;
  color: #5B1111;
  border: 1px solid #5B1111;
  -webkit-box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5), 0 0 2px 2px rgba(144, 50, 50, 0.8);
          box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5), 0 0 2px 2px rgba(144, 50, 50, 0.8);
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.information-name {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid #5B1112;
}
.information-name b {
  margin-right: 0.5em;
  font-size: 14px;
  color: #000;
}
.infomation-wrapper p {
  line-height: 1.5;
}
.main-content-wrapper {
  width: 100%;
  height: 100%;
}
.rank-active {
  -webkit-transform: scale3d(0, 0, 1);
          transform: scale3d(0, 0, 1);
}
.rank-active:nth-child(1) {
  -webkit-animation: bobble 0.8s linear 0.1s infinite;
          animation: bobble 0.8s linear 0.1s infinite;
  will-change: opacity;
}
.rank-active:nth-child(2) {
  -webkit-animation: bobble 0.8s linear 0.4s infinite;
          animation: bobble 0.8s linear 0.4s infinite;
  will-change: opacity;
}
.rank-active:nth-child(3) {
  -webkit-animation: bobble 0.8s  linear 0.6s infinite;
          animation: bobble 0.8s  linear 0.6s infinite;
  will-change: opacity, transform;
}
@-webkit-keyframes bobble {
0% {
    opacity: 0.8;
    -webkit-transform: scale3d(1.3, 1.3, 1);
            transform: scale3d(1.3, 1.3, 1);
}
50% {
    opacity: 0.5;
    -webkit-transform: scale3d(0.9, 0.9, 1);
            transform: scale3d(0.9, 0.9, 1);
}
100% {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}
@keyframes bobble {
0% {
    opacity: 0.8;
    -webkit-transform: scale3d(1.3, 1.3, 1);
            transform: scale3d(1.3, 1.3, 1);
}
50% {
    opacity: 0.5;
    -webkit-transform: scale3d(0.9, 0.9, 1);
            transform: scale3d(0.9, 0.9, 1);
}
100% {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
}



