
.page-charge-group-time {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.example-details{
  position: absolute;
  left:3rem;
  top:0.4rem;
  padding: 5px 15px;
  background: #5B1111;
  color: #fff;
  border-radius: 8px;
  font-size: 12px;
  -webkit-box-shadow: 3px 3px 3px rgba(0,0,0,0.2);
          box-shadow: 3px 3px 3px rgba(0,0,0,0.2);
}
.example-details-assay {
  position: absolute;
  left:3rem;
  top: 30px;
  width:4.3rem;
  background: #ffffff;
  -webkit-box-shadow: 2px 2px 2px rgba(0,0,0,0.2), 0 0 2px 2px rgba(0,0,0,0.1);
          box-shadow: 2px 2px 2px rgba(0,0,0,0.2), 0 0 2px 2px rgba(0,0,0,0.1);
  padding: 0.8em;
  font-size: 12px;
}
.example-details-assay p {
  margin-bottom: 10px;
}
.example-details-assay .info-name {
  font-weight: 300;
  margin-bottom: 0.5em;
}
.active-block{
  -webkit-animation: bobble 1s linear infinite alternate;
          animation: bobble 1s linear infinite alternate;
}
@-webkit-keyframes bobble {
0% {
    opacity: 0.9;
    -webkit-transform: scale3d(1.05, 1.05,1);
            transform: scale3d(1.05, 1.05,1);
}
50% {
    -webkit-transform: scale3d(0.95, 0.95,1);
            transform: scale3d(0.95, 0.95,1);
    opacity: 0.95;
}
100% {
    -webkit-transform: scale3d(1, 1,1);
            transform: scale3d(1, 1,1);
    opacity: 1;
}
}
@keyframes bobble {
0% {
    opacity: 0.9;
    -webkit-transform: scale3d(1.05, 1.05,1);
            transform: scale3d(1.05, 1.05,1);
}
50% {
    -webkit-transform: scale3d(0.95, 0.95,1);
            transform: scale3d(0.95, 0.95,1);
    opacity: 0.95;
}
100% {
    -webkit-transform: scale3d(1, 1,1);
            transform: scale3d(1, 1,1);
    opacity: 1;
}
}
.close {
  text-align: center;
}
.close img{
  width: 0.5rem;
  margin-top: 5px;
}
.twinkle {
  /* animation: twinkle 1s linear infinite alternate; */
}
@-webkit-keyframes twinkle {
0% {
    opacity: 1;
}
33.33% {
    opacity: 0.9;
}
66.66% {
    opacity: 0.8;
}
100% {
    opacity: 0.7;
}
}
@keyframes twinkle {
0% {
    opacity: 1;
}
33.33% {
    opacity: 0.9;
}
66.66% {
    opacity: 0.8;
}
100% {
    opacity: 0.7;
}
}
.assay{
  position: absolute;
  bottom: 1rem;
  width:7rem;
  margin: 0.25rem;
  padding: 0.8em;
  line-height: 1.5;
  color: #5B1111;
  font-weight: bold;
  -webkit-box-shadow: 3px 3px 4px rgba(0,0,0,0.4), 0 0 2px 2px rgba(0,0,0,0.2);
          box-shadow: 3px 3px 4px rgba(0,0,0,0.4), 0 0 2px 2px rgba(0,0,0,0.2);
}



